// export default PdfViewerWithPagination;
import React, { useState, useRef, useEffect } from 'react';
import { Viewer, Worker, ScrollMode, SpecialZoomLevel } from '@react-pdf-viewer/core';
import { defaultLayoutPlugin } from '@react-pdf-viewer/default-layout';
import '@react-pdf-viewer/core/lib/styles/index.css';
import '@react-pdf-viewer/default-layout/lib/styles/index.css';
import testPdf from "./test.pdf";
import { useSwipeable } from 'react-swipeable';

const workerSrc = `https://unpkg.com/pdfjs-dist@2/build/pdf.worker.min.js`;

const PdfViewerWithPagination = () => {
  const [currentPage, setCurrentPage] = useState(0);
  const [totalPages, setTotalPages] = useState(0);
  const [scale, setScale] = useState(1);
  const [isZoomed, setIsZoomed] = useState(false);
  const [canSwipe, setCanSwipe] = useState(true);
  const viewerContainerRef = useRef(null);
  const pagesContainerRef = useRef(null);
  const viewerRef = useRef({
    jumpToPage: () => Promise.resolve(),
    getPagesContainer: () => null
  });

  // 创建分页控制插件
  const paginationPlugin = {
    install: (pluginFunctions) => {
      viewerRef.current = {
        jumpToPage: pluginFunctions.jumpToPage,
        getPagesContainer: pluginFunctions.getPagesContainer
      };
    },
    renderViewer: (renderProps) => {
      const { slot } = renderProps;
      return {
        ...slot,
        children: slot.children
      };
    }
  };

  // 默认布局插件配置
  const defaultLayoutPluginInstance = defaultLayoutPlugin({
    toolbarPlugin: {
      onZoom: (zoom) => {
        const newScale = typeof zoom === 'number' ? zoom : 1;
        setScale(newScale);
        setIsZoomed(newScale > 1);
      },
      onJumpToPage: (pageIndex) => {
        setCurrentPage(pageIndex);
      },
    },
    sidebarTabs: (defaultTabs) => [
      defaultTabs[0] // 只保留缩略图标签
    ]
  });

  useEffect(() => {
    const handleScroll = () => {
      if (!pagesContainerRef.current) return;

      const { scrollTop, scrollHeight, clientHeight } = pagesContainerRef.current;
      const isAtBottom = scrollTop + clientHeight >= scrollHeight - 10; // 10px容差
      setCanSwipe(isAtBottom || !isZoomed);
    };

    const updatePagesContainer = () => {
      if (viewerRef.current.getPagesContainer) {
        const container = viewerRef.current.getPagesContainer();
        if (container) {
          pagesContainerRef.current = container;
          container.addEventListener('scroll', handleScroll);
        }
      }
    };

    // 立即尝试设置容器
    updatePagesContainer();

    // 设置间隔检查容器（后备方案）
    const intervalId = setInterval(updatePagesContainer, 500);

    return () => {
      clearInterval(intervalId);
      if (pagesContainerRef.current) {
        pagesContainerRef.current.removeEventListener('scroll', handleScroll);
      }
    };
  }, [isZoomed]);

  const handleDocumentLoad = (e) => {
    setTotalPages(e.doc.numPages);
  };

  const jumpToPage = (pageIndex) => {
    if (pageIndex >= 0 && pageIndex < totalPages) {
      viewerRef.current.jumpToPage(pageIndex)
        .then(() => setCurrentPage(pageIndex))
        .catch(console.error);
    }
  };

  const goToPreviousPage = () => jumpToPage(currentPage - 1);
  const goToNextPage = () => jumpToPage(currentPage + 1);

  // 手势配置
  const handlers = useSwipeable({
    onSwipedUp: ({ velocity }) => {
      if (velocity > 0.3 && canSwipe) goToNextPage();
    },
    onSwipedDown: ({ velocity }) => {
      if (velocity > 0.3 && canSwipe) goToPreviousPage();
    },
    trackMouse: true,
    delta: 120,
    preventDefaultTouchmoveEvent: true,
    swipeDuration: 500,
    trackTouch: true,
    rotationAngle: 0
  });

  return (
    <div {...handlers} style={{
      height: '100vh',
      display: 'flex',
      flexDirection: 'column',
      backgroundColor: '#f5f5f5',
      touchAction: isZoomed ? 'pan-y' : 'none',
      overflow: 'hidden',
    }}>
      <Worker workerUrl={workerSrc}>
        <div
          ref={viewerContainerRef}
          style={{
            flex: 1,
            overflow: 'hidden',
            position: 'relative'
          }}
        >
          <Viewer
            fileUrl={testPdf}
            plugins={[paginationPlugin, defaultLayoutPluginInstance]}
            scrollMode={ScrollMode.Page}
            onDocumentLoad={handleDocumentLoad}
            onPageChange={(e) => setCurrentPage(e.currentPage)}
            defaultScale={SpecialZoomLevel.PageFit}
          />
        </div>

        {/* 分页控制栏 */}
        <div style={{
          padding: '12px 16px',
          background: '#ffffff',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          gap: '16px',
          borderTop: '1px solid #e0e0e0',
          boxShadow: '0 -2px 8px rgba(0,0,0,0.08)'
        }}>
          <button
            onClick={goToPreviousPage}
            disabled={currentPage === 0}
            style={{
              padding: '8px 16px',
              border: '1px solid #ddd',
              borderRadius: '4px',
              background: '#fff',
              cursor: currentPage === 0 ? 'not-allowed' : 'pointer',
              minWidth: '90px',
              opacity: currentPage === 0 ? 0.6 : 1,
              transition: 'all 0.2s'
            }}
          >
            ← 上一页
          </button>

          <div style={{
            display: 'flex',
            alignItems: 'center',
            gap: '8px',
            fontFamily: 'Arial, sans-serif'
          }}>
            <span style={{
              minWidth: '60px',
              textAlign: 'center',
              fontWeight: 'bold'
            }}>
              {currentPage + 1} / {totalPages}
            </span>
          </div>

          <button
            onClick={goToNextPage}
            disabled={currentPage >= totalPages - 1}
            style={{
              padding: '8px 16px',
              border: '1px solid #ddd',
              borderRadius: '4px',
              background: '#fff',
              cursor: currentPage >= totalPages - 1 ? 'not-allowed' : 'pointer',
              minWidth: '90px',
              opacity: currentPage >= totalPages - 1 ? 0.6 : 1,
              transition: 'all 0.2s'
            }}
          >
            下一页1 →
          </button>
        </div>
      </Worker>
    </div>
  );
};

export default PdfViewerWithPagination;